.movie {
	width: 43%;
	background-color: #fff;
	box-shadow: 8px 8px 20px 1px rgba(50, 50, 93, 0.25);
	padding: 30px;
	margin-bottom: 70px;
	display: flex;
	a {
		max-width: 230px;
		width: 100%;
		margin-right: 30px;
		.movie_img {
			position: relative;
			box-shadow: 0 18px 36px -18px rgba(0, 0, 0, 0.4);
			transform: translateY(0);
			transition: all 0.3s;
			&:hover {
				transform: translateY(-50px);
			}
		}
	}
	.movie_Txt {
		a {
			color: #2c2c2c;
			text-decoration: none;
		}
		h2 {
			a {
				font-size: 24px;

				font-weight: 500;
			}
		}
		.genres {
			list-style: none;
			padding: 0;
			display: flex;
			flex-wrap: wrap;
			gap: 13px;
			li {
				font-size: 14px;
				position: relative;
				background-color: #abb5b8;
				padding: 5px 10px 8px;
				border-radius: 20px;
				color: #fff;
				&::after {
					display: block;
					content: "";
					position: absolute;
					top: 11px;
					right: -7px;
					width: 1px;
					height: 10px;
					background-color: rgba(44, 44, 44, 0.5);
				}
				&:last-child::after {
					display: none;
				}
			}
		}
	}
}

/*반응형*/
@media (max-width: 1200px) {
	.movie {
		width: 100%;
	}
}
@media (max-width: 600px) {
	.movie {
		flex-flow: column;
		margin-bottom: 50px;
		a {
			margin-right: 0;
			max-width: initial;
			.movie_img {
				width: 100%;
				//max-height: 500px;
				height: 100%;
			}
		}
	}
}
@media (max-width: 400px) {
	.movie_img {
		max-height: 375px;
		height: 100%;
	}
}
